* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.1s ease-in-out;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3em;
  padding: 1em;
  min-height: 100vh;
  width: 100%;
  background-color: #1f1f1f;
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}
.wrapper #capture-display {
  max-width: 500px;
  aspect-ratio: 16/9;
  box-shadow: 0 0 20px rgba(255, 99, 71, 0.9);
  border-radius: 10px;
}
.wrapper .btn {
  width: 250px;
  height: 50px;
  background-color: rgba(255, 99, 71, 0.9);
  border: none;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
}
.wrapper .btn:hover {
  background-color: transparent;
  border: 1px solid tomato;
}
.wrapper .btn img {
  width: 30px;
  height: 30px;
}